































































































































import {Component, Vue} from "vue-property-decorator";
import {HttpService} from "@/services/ApiService";


@Component
export default class ConfigRateService extends Vue{
  public isLoading = true

  public tarifa = {
    id: 0,
    distance_base: 0,
    km_additional: 0,
    rate_base: 0,
    initial_shipments: 0,
    final_shipments: 0,
  }

  public service = {
    name: '',
    rates: []
  }

  public indexrate = 0;

  async getService() {
    await HttpService.get(`services/table?filter[id]=${this.$route.params.id}&include=rates` ,
        this.setServices)
  }


  async updateRate() {
    await HttpService.put(`rates/${this.tarifa.id}/service?filter[id]=${this.$route.params.id}&include=rates` , this.tarifa,
        this.setServices)
    // @ts-ignore
    this.$refs.modal.close()
  }

  setServices(data: any){
    console.log(data)
    this.service ={
      name: data.data[0].name,
      rates: data.data[0].rates,
    };
    this.isLoading = false
  }

  openDialog(index: number){
    this.indexrate = index;
    let ser: any = this.service.rates[index];

    this.tarifa ={
      id:ser.id,
      km_additional: ser.km_additional,
      distance_base: ser.distance_base,
      rate_base: ser.rate_base,
      initial_shipments: ser.initial_shipments,
      final_shipments: ser.final_shipments,
    }

    // @ts-ignore
    this.$refs.modal.open()
  }

  created(){
    this.getService();
  }

}
